/* --------------------------------------------------------------
 * = 扩展 BootStrap Dropdown
 * ----------------------------------------------------------- */

.dropdown .dropdown-menu .caret {
  border: 4px solid transparent;
  border-left-color: #000;
  float: right;
  margin-top: 6px;
}

.dropdown-menu {
  min-width: 250px;
  > li.open > a {
    background-color: #f5f5f5;
    color: #262626;
    text-decoration: none;
  }
  .dropdown-menu {
    left: 100%;
    margin: 0;
    right: auto;
    top: -1px;
  }
}

.pull-right .dropdown-menu .dropdown-menu {
  left: auto;
  right: 100%;
}

.dropdownhover-top {
  margin-bottom: 2px;
  margin-top: 0;
  transform-origin: 50% 100%;
  .dropdown-menu {
    bottom: -1px;
    top: auto;
  }
}