/* -----------------------------------------------------------
 * = 超级菜单
 * ----------------------------------------------------------- */
nav.bootsnav .dropdown.megamenu-fw {
  position: static;
  .dropdown-menu {
    left: 0;
    right: 0;
  }
}

nav.bootsnav .container {
  position: relative;
}

nav.bootsnav .megamenu-fw .dropdown-menu {
  left: auto;
}

nav.bootsnav .megamenu-content {
  padding: 15px;
  width: 100% !important;
  .title {
    margin-top: 0;
  }
}

/* -----------------------------------------------------------
 * = 导航条
 * ----------------------------------------------------------- */
nav.navbar.bootsnav {
  margin-bottom: 0;
  border-radius: 0;
  background-color: @menu-bg;
  border: none;
  z-index: 9;
  ul.nav > li > a {
    color: @menu-link-color;
    background-color: transparent;
    outline: none;
  }
  ul.nav li.active > a {
    color: @menu-active-link-color;
    background-color: @menu-dropdown-active-bg;
  }
  .navbar-toggle {
    background-color: transparent;
    border: none;
    padding: 0;
    font-size: 18px;
    position: relative;
    top: 8px;
    .icon-bar {
      background-color: @menu-link-color;
    }
  }
  ul.nav .dropdown-menu .dropdown-menu {
    top: 0;
    left: 100%;
  }
  ul.nav ul.dropdown-menu > li > a {
    white-space: normal;
  }
}

ul.menu-col {
  padding: 0;
  margin: 0;
  list-style: none;
  li a {
    color: #6f6f6f;
    &:focus {
      text-decoration: none;
    }
  }
}


/* -----------------------------------------------------------
 * = 全宽
 * ----------------------------------------------------------- */
nav.bootsnav.navbar-full {
  padding-bottom: 10px;
  padding-top: 10px;
  .navbar-header {
    display: block;
    width: 100%;
  }
  .navbar-toggle {
    display: inline-block;
    margin-right: 0;
    position: relative;
    top: 0;
    font-size: 30px;
    transition: all 1s ease-in-out;
  }
  .navbar-collapse {
    position: fixed;
    width: 100%;
    height: 100% !important;
    top: 0;
    left: 0;
    padding: 0;
    display: none !important;
    z-index: 9;
    &.in {
      display: block !important;
    }
    .nav-full {
      overflow: auto;
      &::-webkit-scrollbar {
        width: 0;
      }
    }
    .wrap-full-menu {
      display: table-cell;
      vertical-align: middle;
      background-color: @menu-dropdown-bg;
      overflow: auto;
    }
    ul.nav {
      display: block;
      width: 100%;
      overflow: auto;
      a {
        background-color: transparent;
      }
      > li {
        float: none;
        display: block;
        text-align: center;
        > a {
          display: table;
          margin: auto;
          text-transform: uppercase;
          font-weight: bold;
          letter-spacing: 2px;
          font-size: 24px;
          padding: 10px 15px;
        }
      }
    }
  }
}

li.close-full-menu {
  padding-top: 30px;
  padding-bottom: 30px;
  > a {
    padding-top: 0;
    padding-bottom: 0;
  }
}


/* -----------------------------------------------------------
 * = 属性导航
 * ----------------------------------------------------------- */
.attr-nav {
  float: right;
  display: inline-block;
  margin-left: 13px;
  margin-right: -15px;
  > ul {
    padding: 0;
    margin: 0 0 -7px 0;
    list-style: none;
    display: inline-block;
    > li {
      float: left;
      display: block;
      &.dropdown ul.dropdown-menu {
        border-radius: 0;
        box-shadow: 0 0 0;
        border: solid 1px @menu-dropdown-border;
      }
      > a {
        color: #6f6f6f;
        display: block;
        padding: 28px 15px;
        position: relative;
        span.badge {
          position: absolute;
          top: 50%;
          margin-top: -15px;
          right: 5px;
          font-size: 10px;
          padding: 0;
          width: 15px;
          height: 15px;
          padding-top: 2px;
        }
      }
    }
  }
}

ul.cart-list {
  padding: 0 !important;
  width: 250px !important;
  > li {
    position: relative;
    border-bottom: solid 1px #efefef;
    padding: 15px 15px 23px 15px !important;
    &.total {
      background-color: #f5f5f5;
      padding-bottom: 15px !important;
      > .btn {
        display: inline-block;
        border-bottom: solid 1px #efefef;
      }
      > span {
        padding-top: 8px;
      }
    }
    > a.photo {
      padding: 0 !important;
      margin-right: 15px;
      float: left;
      display: block;
      width: 50px;
      height: 50px;
      left: 15px;
      top: 15px;
    }
    img {
      width: 50px;
      height: 50px;
      border: solid 1px #efefef;
    }
    > h6 {
      margin: 0;
      > a.photo {
        padding: 0 !important;
        display: block;
      }
    }
    > p {
      margin-bottom: 0;
    }
    .price {
      font-weight: bold;
    }
  }
}


/* -----------------------------------------------------------
 * = 搜索
 * ----------------------------------------------------------- */
.top-search {
  background-color: #333;
  padding: 10px 0;
  display: none;
  input.form-control {
    background-color: transparent;
    border: none;
    box-shadow: 0 0 0;
    color: #fff;
    height: 40px;
    padding: 0 15px;
  }
  .input-group-addon {
    background-color: transparent;
    border: none;
    color: #fff;
    padding-left: 0;
    padding-right: 0;
    &.close-search {
      cursor: pointer;
    }
  }
}


/* -----------------------------------------------------------
 * = 侧边导航
 * ----------------------------------------------------------- */
body {
  transition: all 0.3s ease-in-out;
}

body.on-side {
  margin-left: -280px;
}

.side {
  position: fixed;
  overflow-y: auto;
  top: 0;
  right: -280px;
  width: 280px;
  padding: 25px 30px;
  height: 100%;
  display: block;
  background-color: #333;
  transition: all 0.3s ease-in-out;
  z-index: 9;
  &.on {
    right: 0;
  }
  .close-side {
    float: right;
    color: #fff;
    position: relative;
    z-index: 2;
    font-size: 16px;
  }
  .widget {
    position: relative;
    z-index: 1;
    margin-bottom: 25px;
    .title {
      color: #fff;
      margin-bottom: 15px;
    }
    ul.link {
      padding: 0;
      margin: 0;
      list-style: none;
      li a {
        color: #9f9f9f;
        letter-spacing: 1px;
        &:hover {
          color: #fff;
          text-decoration: none;
        }
      }
    }
  }
}


/* -----------------------------------------------------------
 * = 分享
 * ----------------------------------------------------------- */
nav.navbar.bootsnav .share {
  padding: 0 30px;
  margin-bottom: 30px;
  ul {
    display: inline-block;
    padding: 0;
    margin: 0 0 -7px 0;
    list-style: none;
    > li {
      float: left;
      display: block;
      margin-right: 5px;
      > a {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        background-color: #cfcfcf;
        color: #fff;
      }
    }
  }
}


/* -----------------------------------------------------------
 * = 透明
 * ----------------------------------------------------------- */
nav.navbar.bootsnav.navbar-fixed {
  position: fixed;
  display: block;
  width: 100%;
}

nav.navbar.bootsnav.no-background {
  transition: all 1s ease-in-out;
}


/* -----------------------------------------------------------
 * = 固定
 * ----------------------------------------------------------- */
.wrap-sticky {
  position: relative;
  transition: all 0.3s ease-in-out;
  nav.navbar.bootsnav {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    &.sticked {
      position: fixed;
      transition: all 0.2s ease-in-out;
    }
  }
}

body.on-side .wrap-sticky nav.navbar.bootsnav.sticked {
  left: -280px;
}


