u {
  text-decoration: underline;
}
/* 标记，类似于手写的荧光笔的作用 */
mark {
  padding: 2px;
  margin: 0 5px;
}
/* 底部印刷体、版本等标记 */
small,
figcaption {
  font-size: 0.9em;
  color: #888;
}
strong,
b {
  font-weight: bold;
  color: #333333;
}
/* 嵌入内容自适应 */
embed,
iframe,
object {
  max-width: 100%;
}
/* 可拖动文件添加拖动手势 */
[draggable] {
  cursor: move;
}
/* 强制文本换行 */
.textwrap,
.textwrap td,
.textwrap th {
  word-wrap: break-word;
  word-break: break-all;
