 * ----------------------------------------------------------- */
.bx-wrapper .bxslider {
  margin: 0;
  padding: 0;
}
ul.bxslider {
  list-style: none;
}
.bx-viewport {
  -webkit-transform: translatez(0);
}
.bx-wrapper {
  position: relative;
  margin: 0 auto;
  padding: 0;
